import MultipleChoiceForm from "./MultipleChoiceForm";

const question = "What pets do you have?";
const inputName = "pets";

const displayValueMap = [
  ["Dogs", "dog"],
  ["Cats", "cat"]
];

const PetForm = ({ onChange, value }) =>
  MultipleChoiceForm({
    question,
    inputName,
    displayValueMap,
    onChange,
    checked: value
  });

export { inputName, PetForm };
